*{
  background-color: rgb(23, 41, 81);
  color:white;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
 
}
.header{
  padding-left:1.5rem;
}

.card{
  display:flex;
  margin-bottom:2rem;
  
}

.image{
  margin:0.5rem;
}

.img-border{
  width:100px;
  height:100px;
  border-radius: 50%;
  margin:10px;
  
  outline:3px solid turquoise;;
}

.img{
  width:100px;
  height:100px;
  border-radius: 50%;
  margin:10px;
 
}

.text{
  display:flex;
  flex-direction: column;
  justify-content: center;
  
 
}

.details{
  display:flex;
  justify-content: flex-start;
}

.name{
  font-size:140%;
  font-weight:5;
}
.occurance{
  color:grey;
  font-size: small;
  padding-left:1rem;
  padding-top:0.7rem;
  
}

.text h2{
  margin-top:5px;
}